@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}

* {
  padding: 0 auto;
  margin: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.send-eth-component {
  width: 30%;
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  white-space: nowrap;
  font-family: "Quicksand", sans-serif;
}
.send-eth-component_header {
  background-color: #753a86;
  border-radius: 2rem 2rem 0 0;
  font-size: 3.5rem;
  justify-content: flex-start;
  text-align: start;
}
.send-eth-component_icon {
  margin-left: 3rem;
}
.send-eth-component_main-content {
  background-color: white;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  height: 100%;
  text-align: start;
  border-left: 1px solid #f7f5f7;
  border-right: 1px solid #f7f5f7;
}
.send-eth-component_sub-content1 {
  padding: 4rem 1rem;
  grid-gap: 2rem;
  gap: 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: start;
  border-bottom: 1px solid #f7f5f7;
}
.send-eth-component_sub-content2 {
  height: 100%;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 2.5rem;
  text-align: start;
}
.send-eth-component_sub-content1-link {
  text-align: center;
  cursor: pointer;
}
.send-eth-component_sub-content1-user-address {
  display: flex;
  justify-content: flex-start;
  grid-gap: 1rem;
  gap: 1rem;
}
.send-eth-component_content-send-to-friend {
  border-top: 1px solid #f7f5f7;
}
.send-eth-component_footer {
  background-color: #753a86;
  border-radius: 0 0 2rem 2rem;
  padding: 1.5rem;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}
.send-eth-component_sub-content2-ether {
  display: flex;
  justify-content: center;
  color: #753a86;
  padding: 5rem 0;
}
.send-eth-component_sub-content2-ether input {
  color: #753a86;
  border: 0px;
  font-size: 6.5rem;
  width: 4rem;
}
.send-eth-component_sub-content2-ether label {
  display: flex;
  align-items: flex-end;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.send-eth-component_sub-content1-account-balance {
  display: flex;
  justify-content: flex-start;
  grid-gap: 1rem;
  gap: 1rem;
}

@media (min-width: 320px) {
  .send-eth-component {
    width: 80%;
  }
}
@media (min-width: 480px) {
  .send-eth-component {
    width: 50%;
  }
}
@media (min-width: 600px) {
  .send-eth-component {
    width: 45%;
  }
}
@media (min-width: 801px) {
  .send-eth-component {
    width: 35%;
  }
}
@media (min-width: 1025px) {
  .send-eth-component {
    width: 30%;
  }
}
@media (min-width: 1281px) {
  .send-eth-component {
    width: 25%;
  }
}

